import React from "react";

const quote1 = <><h2>Let's go invent<br />tomorrow instead<br /><span>of worrying<br />about what<br />happened<br /></span>yesterday</h2><p>Steve Jobs</p></>;
const quote2 = <><h2>The very essence<br />of instinct is that<br /><span>it's followed<br />independently<br /></span>of reason</h2><p>Charles Darwin</p></>;
const quote3 = <><h2>Everything<br />must<br /><span>be made as simple<br />as possible<br /></span>but not simpler</h2><p>Albert Einstein</p></>;
const quote4 = <><h2>what is right<br />is not always<br /><span>popular<br />and what is<br />popular is not<br /></span>always right</h2><p>Albert Einstein</p></>;
const quote5 = <><h2>Normality<br />is a paved road:<br /><span>It's comfortable<br />to walk, but<br /></span>no flowers<br/>grow on it</h2><p>Vincent van Gogh</p></>;

const quotes = [quote1, quote2, quote3, quote4, quote5];


const QuotesRandom = () => {
    const rI = Math.floor(Math.random() * quotes.length);
        const selectQ = quotes[rI];
        return <div className="quotes" data-crsr-eye>{selectQ}</div>
}

export default QuotesRandom;