import React, { useEffect, useRef } from "react";
import "./style.css";
import { gsap } from "gsap";

import { Link, useRouteMatch } from "react-router-dom";
import { isBrowser, isMobile } from "react-device-detect";

import { ContactNav } from "./Contact";
import { Toggle } from "../Toggle"

import img1 from "./images/image1.jpg";
import img2 from "./images/image2.jpg";
import img3 from "./images/image3.jpg";
import img4 from "./images/image4.jpg";

function OldSchoolMenuLink({ label, to, activeOnlyWhenExact }) {
    let match = useRouteMatch({
        path: to,
        exact: activeOnlyWhenExact
    });
    return (
        <Link className={match ? "active" : ""} to={to} data-crsr-zoomout >{label}</Link>
    );
}

const homeSpan = () => <h2><span>h</span><span>o</span><span>m</span><span>e</span></h2>;
const aboutSpan = () => <h2><span>a</span><span>b</span><span>o</span><span>u</span><span>t</span></h2>;
const worksSpan = () => <h2><span>w</span><span>o</span><span>r</span><span>k</span><span>s</span></h2>;
// const contactSpan = () => <h2><span>c</span><span>o</span><span>n</span><span>t</span><span>a</span><span>c</span><span>t</span></h2>;
