const Navigation = () => {

    const nmBtn = useRef(null);
    const navMenu = useRef(null);
    let navMenuC = useRef(null);
    const navMenuCa = useRef(null);
    const varyaLoh = useRef(null);

    useEffect(() => {
        const navMenuA = document.querySelectorAll('.nav-menu-l ul li a');
        const navMenuAB = document.querySelector('.nav-menu-l ul');
        const logo = document.querySelector('.logo');
        const cRight = document.querySelector('.c-right');
        const themeApp = document.querySelector('.theme-app');
        const bfix = document.body;
        varyaLoh.current.remove();

        nmBtn.current.addEventListener('click', () => {
            nmBtn.current.classList.toggle('act');
            navMenu.current.classList.toggle('act');
            logo.classList.toggle('act');
            bfix.classList.toggle('act');
        })

        const closeMenu = () => {
            let t1 = 700;
            if (isMobile) { t1 = 500 };
            setTimeout(() => {
                bfix.classList.remove('act');
                navMenu.current.className = "nav-menu no-act";
                nmBtn.current.className = "nm-btn no-act";
            }, t1);
            setTimeout(() => {
                navMenu.current.className = "nav-menu";
                nmBtn.current.className = "nm-btn";
                logo.classList.remove('act');
            }, 1200);
            setTimeout(() => {
                logo.classList.remove('act');
            }, 200);
        }

        navMenuA.forEach(i => {
            i.addEventListener('click', closeMenu);
            i.addEventListener('click', () => {
                navMenuAB.style.pointerEvents = "none";
                setTimeout(() => navMenuAB.style.pointerEvents = "painted", 700)
            });
        });
        logo.addEventListener('click', closeMenu);

        if (isBrowser) {
            navMenuA.forEach((event, image) => {
                event.addEventListener('mouseenter', () => {
                    navMediaArr()
                    gsap.fromTo(navMenuC.current, { scale: 1.4 }, { opacity: 1, scale: 1.1, duration: .8, ease: "Power3.easeOut" });
                    navMenuCa.current.style.opacity = 0;
                    gsap.to([cRight, themeApp], .2, { opacity: 0 });
                })
                event.addEventListener('mouseleave', () => {
                    navMediaArr()
                    gsap.fromTo(navMenuC.current, { opacity: 1, scale: 1.1 }, { opacity: 0, scale: 1, duration: .8, ease: "Power3.easeOut" });
                    navMenuCa.current.style.opacity = 1;
                    gsap.to([cRight, themeApp], .2, { opacity: 1 });
                })
                event.addEventListener('click', () => {
                    navMenuCa.current.style.opacity = 0;
                })

                function navMediaArr() {
                    navMenuC.current.style.backgroundImage =
                        (image === 0) ? `url("${img1}")` :
                            (image === 1) ? `url("${img3}")` :
                                (image === 2) ? `url("${img2}")` :
                                    (image === 3) ? `url("${img4}")` : null;
                };

            });
        }

    }, [])

    return (
        <>
            <div className="navigation">
                <OldSchoolMenuLink activeOnlyWhenExact={true} to="/" label={homeLogo()} />
                <div className="nm-btn" ref={nmBtn} data-crsr-zoomout>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 437 437"><path d="M404.1,134H32.9c-15.4,0-27.8-12.5-27.8-27.8v0c0-15.4,12.5-27.8,27.8-27.8h371.1c15.4,0,27.8,12.5,27.8,27.8v0C431.9,121.6,419.4,134,404.1,134z"></path><path d="M404.1,358.7H32.9c-15.4,0-27.8-12.5-27.8-27.8v0c0-15.4,12.5-27.8,27.8-27.8h371.1c15.4,0,27.8,12.5,27.8,27.8v0C431.9,346.2,419.4,358.7,404.1,358.7z"></path><path d="M404.1,246.3H32.9c-15.4,0-27.8-12.5-27.8-27.8v0c0-15.4,12.5-27.8,27.8-27.8h371.1c15.4,0,27.8,12.5,27.8,27.8v0C431.9,233.9,419.4,246.3,404.1,246.3z"></path></svg>
                </div>
                <div className="nav-menu" ref={navMenu}>
                    <div className="nav-menu-l">
                        <ul>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/" label={homeSpan()} /></li>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/about" label={aboutSpan()} /></li>
                            <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/blog" label={worksSpan()} /></li>
                            {/* <li><OldSchoolMenuLink activeOnlyWhenExact={true} to="/contact" label={contactSpan()} /></li> */}
                        </ul>
                    </div>
                    <Toggle />
                    <div className="nav-menu-c">
                        <div className="nav-menu-c-b" ref={navMenuC}>
                            <div ref={varyaLoh}>
                                <img src={img1} alt="home" />
                                <img src={img2} alt="blog" />
                                <img src={img3} alt="about" />
                                {/* <img src={img4} alt="contact" /> */}
                            </div>
                        </div>
                        <div className="nav-menu-c-a" style={{ padding: '0 20%', color: '#f9f9f9' }}>
                            <div ref={navMenuCa}>
                                <ContactNav />
                            </div>
                        </div>
                    </div>
                    <div className="c-right"><p>2022  Ivanwebstudio.<br /> All rights reserved.</p></div>
                </div>
            </div>
        </>
    )
}

export default Navigation;