export const vPlayer = (link, poster) => {
    const vPlayer = document.querySelector('.v-player');
    const vCont = document.getElementById('v-cont');
    if (vCont) {
        vCont.addEventListener('contextmenu', e => e.preventDefault());
        vCont.addEventListener('click', () => vPlayer.classList.add('act'));
        console.log('aedsawd', vPlayer)
    }
    return (
        <div className='v-player'>
            <video id="v-cont" controls controlsList="noplaybackrate nodownload" disablePictureInPicture={true} poster={poster} >
                <source type="video/mp4" data-reactid=".0.1.0.0.0" src={link} />
            </video>
        </div>
    )
}