import React from "react";
import { Link } from "react-router-dom";
import { isBrowser, isMobile } from "react-device-detect";
import { useDataJson, styleDataJson, blogItems } from "../../../hooks/useDataJson";

import { HoverCursor } from "../../../components/CustomCursor/CursorHoverSeiitngs";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import { ScrollBlock } from "../../../hooks/ScrollBlock";

// import { vPlayer } from "../../../hooks/VPlayer";

gsap.registerPlugin(ScrollTrigger);

const animaHeaderElements = () => {

  // ----------- animation header elements

  HoverCursor();

  const imageHeaderAnim = document.querySelector('.img-cont');
  const imgContAnim = document.querySelector('.img-cont-anim');
  const titleHeaderAnim = document.querySelector('.title-header-anim');
  const pageContent = document.querySelector('.page-content');
  const contScrll = document.querySelectorAll('.cont-scrll');
  const contImgprlx = document.querySelector('.cont-img-prlx');
  const contImgprlxImg = document.querySelector('.cont-img-prlx img');
  const vPlayer = document.querySelectorAll('.v-player');

  if (imageHeaderAnim) {

    if (isBrowser) {
      if (vPlayer) {
        vPlayer.forEach(i => {
          i.addEventListener('contextmenu', e => e.preventDefault());
          i.addEventListener('click', () => i.classList.add('act'));
        });
      }
    }
    if (isMobile) {
      if (vPlayer) {
        vPlayer.forEach(i => {
          i.addEventListener('contextmenu', e => e.preventDefault());
          i.classList.add('act');
        });
      }
    }

    setTimeout(() => {
      pageContent.style.visibility = 'hidden'
      pageContent.style.opacity = 0;
      titleHeaderAnim.style.opacity = 0;

      gsap.fromTo(imageHeaderAnim, 3, { scale: 1.3 }, { scale: 1, ease: "power2" });
      gsap.to(imgContAnim, .8, { height: '50vh', ease: "power2", delay: .8 });
      gsap.fromTo(titleHeaderAnim, 1.3, { x: '8vw', opacity: 0 }, { x: '0', opacity: 1, ease: "power2", delay: 1.4 });
      gsap.to(pageContent, .8, { visibility: 'visible', opacity: 1, ease: "power2", delay: 2 });


      const welcome = gsap.timeline({
        scrollTrigger: {
          trigger: ".sth-scrl",
          scroller: ".sth-scrl",
          scrub: true,
          start: "top top",
          end: "+=100%",
          ease: "none"
        }
      });
      welcome.to(imageHeaderAnim, { y: "20vh" });

      contScrll.forEach(i => {
        i.style.cssText = `transform: translateY(30%)`;
        gsap.to(i, {
          scrollTrigger: {
            trigger: i,
            scroller: ".sth-scrl",
            start: "top bottom",
            // markers: true,
            scrub: true,
            end: "bottom bottom"
          }, transform: 'translateY(0)'
        });
      });

      if (contImgprlx || contImgprlxImg) {
        gsap.to(contImgprlxImg, {
          scrollTrigger: {
            trigger: contImgprlx,
            scroller: ".sth-scrl",
            start: "top bottom",
            scrub: true,
            end: "+=300%",
            ease: "none"
          }, y: '20%'
        }, '-=6');
      }

    }, 500)
  };
}

export function Item({ id }) {

  const [itemsData, isLoaded, error] = useDataJson(blogItems.url);

  ScrollBlock(2000);

  // ----------- Page navigation

  return (
    <>
      {(error) ? <div className="l--data" style={styleDataJson.loadError}><h4>Error: {error.message}</h4></div> : (!isLoaded) ? <div style={styleDataJson.loadError}><h4>Loading...</h4></div> :
        <>
          <div className="card-content-container" style={{ marginTop: '50vh' }}>
            <div className="img-cont-anim">
              {/* <Link className="link-img-cont" to="/blog/" data-crsr-back /> */}
              <div className="img-cont" style={{ backgroundImage: `url(/testing/assets/img/${id}.jpg)` }} />
            </div>
            {itemsData.map(item => {
              const { category, title, text, text2, images, images2, imagesParalax, titlesmall, titlesmall2, youtubeEmbed } = item;

              if (item.id === id) {

                const pagePrev = () => {
                  let pageSrc = (id.substr(4) * 1) + 1;
                  if (pageSrc > itemsData.length) {
                    return ``
                  } else {
                    return `p-n/p-00${pageSrc}`
                  }
                };
                const pageNext = () => {
                  let pageSrc = (id.substr(4) * 1) - 1;
                  if (pageSrc === 0) {
                    return ``
                  } else {
                    return `p-n/p-00${pageSrc}`
                  }
                };

                return (
                  <div key={id} ref={() => animaHeaderElements()}>
                    <div className="container">
                      <span className="category">{category}</span>
                      <h2 className="title-header-anim" style={{ margin: 'calc(30px + 4vw) 0' }}>{title}</h2>
                      <div className="page-content">
                        <p>{text}</p>
                        {(titlesmall) ? <h3 className="cont-scrll-txt" style={{ margin: 'calc(30px + 4vw) 0' }}>{titlesmall}</h3> : null}
                        {(images) ? images.map((value, index) => (
                          <div className="cont-scrll" key={index}>
                            <div className="cont-img-box">
                              <img src={value.src} alt={value.alt} style={{ width: '100%', height: '100%', position: 'absolute', left: 0, top: 0, overflow: 'hidden', objectFit: 'cover', background: '#ccc', userSelect: 'none' }} data-crsr-zoomout />
                            </div>
                            <p>{value.text}</p>
                          </div>
                        )) : null}

                      </div>
                    </div>
                    {(imagesParalax) ? <div className="cont-img-prlx"><img src={imagesParalax} alt='img-prlx' /></div> : null}
                    <div className="container" style={{ paddingTop: 0 }}>
                      <div className="page-content">
                        {(titlesmall2) ? <h3 className="cont-scrll-txt" style={{ margin: 'calc(30px + 4vw) 0' }}>{titlesmall2}</h3> : null}
                        {(text2) ? <p>{text2}</p> : null}
                        {(images2) ? images2.map((value, index) => (
                          <div className="cont-scrll" key={index}>
                            <div className="cont-img-box">
                              <img src={value.src} alt={value.alt} style={{ width: '100%', height: '100%', position: 'absolute', left: 0, top: 0, overflow: 'hidden', objectFit: 'cover', background: '#ccc', userSelect: 'none' }} data-crsr-zoomout />
                            </div>
                            <p>{value.text}</p>
                          </div>
                        )) : null}
                        {/* {(youtubeEmbed) ? youtubeEmbed.map(i => <div key={i.url} className="t---pl cont-scrll"> {vPlayer(i.url, i.poster)} </div>) : null} */}
                        {(youtubeEmbed) ? youtubeEmbed.map(i => <div className='v-player cont-scrll' key={i.url}><video id="v-cont" controls controlsList="noplaybackrate nodownload" disablePictureInPicture={true} poster={i.poster} ><source type="video/mp4" data-reactid=".0.1.0.0.0" src={i.url} /></video></div>) : null}
                        <div className="content-pagination">
                          <Link to={`/blog/${pagePrev()}`} data-crsr-prev><h2>PREV</h2></Link>
                          <Link to="/blog/" data-crsr-back><h4>works</h4></Link>
                          <Link to={`/blog/${pageNext()}`} data-crsr-next><h2>NEXT</h2></Link>
                          {/* <button onClick={() => pageNext()} data-crsr-next>NEXT</button> */}
                        </div>
                      </div>
                    </div>
                  </div>
                )
              }
              return null
            })}
          </div>
        </>
      }
    </>
  )
}
