import React, { useEffect, useRef, useState } from "react";
import "./home.css";

import { BrowserView, MobileView, isBrowser, isMobile } from "react-device-detect";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocoScroll from "../../hooks/useLocoScroll";
import { HoverCursor } from "../../components/CustomCursor/CursorHoverSeiitngs";

import img7 from "./testpageImg/7.jpg";
import { PagePagination } from "./PagePagination"
import Footer from "../../components/footer/Footer";
import Quotes from "./QuotesRandom";

import { useDataJson, styleDataJson, blogItems } from "../../hooks/useDataJson";

import { Link } from "react-router-dom";

import { ScrollBlock } from "../../hooks/ScrollBlock";

gsap.registerPlugin(ScrollTrigger);

function Home() {

    const [itemsData, isLoaded, error] = useDataJson(blogItems.url);
    const [contLoaded, setcontLoaded] = useState(false);

    /////////////////////////   Page scroll animation

    const LScroll = LocoScroll();
    const scrollDown = useRef(null);

    ScrollBlock(3000);

    useEffect(() => {

        HoverCursor();

        const preload = document.querySelector('.preloader');
        const quotesH2 = document.querySelector('.quotes h2');
        const quotesP = document.querySelector('.quotes p');
        const tr1 = document.querySelector('.tr--1');

        // if (preload) {
        //     console.log('bingo!!!')
        // }

        if (!preload) {
            if (isBrowser) { quotesH2.style.cssText = `opacity: 0; transform:translateX(20%); letter-spacing: .6vw;`; }
            if (isMobile) { quotesH2.style.cssText = `opacity: 0; transform:translateX(20%);` }
            quotesP.style.cssText = `opacity: 0; transform: translateX(30%);`;
            tr1.style.cssText = `bottom: -100%`;
            setTimeout(() => {
                gsap.to(quotesH2, 2, { transform: 'translateX(0%)', letterSpacing: '-0.1vw', opacity: 1, ease: "Power4.easeOut" });
                gsap.to(quotesP, 2, { transform: 'translateX(0%)', opacity: 1, ease: "Power4.easeOut" });
                gsap.to(tr1, 2.5, { bottom: '0', delay: .3, ease: "Power4.easeOut" });
            }, 1000);
        }

        if (contLoaded) {
            setTimeout(() => {
                if (scrollDown) {
                    welcome.to(scrollDown.current, .4, { opacity: 0, delay: 1 }, '-=6');
                }
            }, 700);
            const welcome = gsap.timeline({
                scrollTrigger: {
                    trigger: ".blue",
                    scroller: ".sth-scrl",
                    scrub: true,
                    // pin: true,
                    // markers: true,
                    start: "top top",
                    end: "+=120%",
                    ease: "none"
                }
            });
            welcome
                .to('.quotes h2', 6, { y: "20vh" }, '-=6')
                .to('.quotes p', 6, { y: "22vh" }, '-=6')
                .to('.tr--1', 6, { y: "32vh" }, '-=6');

            gsap.from(".color-lines", {
                scrollTrigger: {
                    trigger: ".color-lines",
                    scroller: ".sth-scrl",
                    scrub: true,
                    start: "top bottom",
                    end: "+=150%",
                    toggleClass: 'act-h'
                },
            });

            gsap.from(".line-1", {
                scrollTrigger: {
                    trigger: ".line-1",
                    scroller: ".sth-scrl",
                    scrub: true,
                    start: "top bottom",
                    end: "top top"
                },
                scaleX: 0,
                transformOrigin: "left center",
                ease: "none"
            });

            if (isBrowser) {
                const t3 = gsap.timeline({
                    scrollTrigger: {
                        trigger: ".orange",
                        scroller: ".sth-scrl",
                        scrub: true,
                        pin: true,
                        start: "top top",
                        invalidateOnRefresh: true,
                        end: "+=100%"
                    }
                });

                t3.from(".line-2", { scaleX: 0, transformOrigin: "left center", ease: "none", duration: 6 }, '-=6')
                    .fromTo(".bp-7", { scale: 1.5, opacity: 0 }, { scale: 1, opacity: 1, ease: "none", duration: 6 }, '-=6');
                gsap.to(".bp-7", {
                    scrollTrigger: {
                        trigger: "#sectionPin",
                        scroller: ".sth-scrl",
                        scrub: true,
                        start: "top bottom",
                        end: "top top"
                    },
                    y: '30%', ease: "none"
                }, '-=6');

                let pinWrap = document.querySelector(".pin-wrap");
                gsap.to(pinWrap, {
                    scrollTrigger: {
                        scroller: ".sth-scrl",
                        scrub: true,
                        trigger: "#sectionPin",
                        pin: true,
                        invalidateOnRefresh: true,
                        // anticipatePin: 1,
                        start: "top top",
                        end: () => "+=" + pinWrap.offsetWidth / 1.5
                    },
                    x: () => -(pinWrap.scrollWidth - document.documentElement.clientWidth) + "px",
                    ease: "none",
                });

                let proxy = { skew: 0 },
                    skewSetter = gsap.quickSetter(".skewElem", "skewX", "deg"),
                    clamp = gsap.utils.clamp(-20, 20);

                ScrollTrigger.create({
                    scroller: ".sth-scrl",
                    trigger: "#sectionPin",
                    start: "top top",
                    end: () => "+=" + pinWrap.offsetWidth / 1.5,
                    onUpdate: self => {
                        let skew = clamp(self.getVelocity() / -300);
                        if (Math.abs(skew) > Math.abs(proxy.skew)) {
                            proxy.skew = skew;
                            gsap.to(proxy, { skew: 0, duration: 0.3, ease: "power3", overwrite: true, onUpdate: () => skewSetter(proxy.skew) });
                        }
                    }
                });
                gsap.set(".skewElem", { force3D: true }); // - fix 
            };
        };

        ScrollTrigger.refresh();

    }, [contLoaded]);

    return (
        <>
            {/* <ScrollBlock /> */}
            <div className="sth-scrl" ref={LScroll}>
                <BrowserView>
                    <PagePagination />
                </BrowserView>
                <section id="home" className="panel blue pre-anim">
                    <div className="wel-h2 skewElem">
                        <Quotes />
                        <div className="color-lines">
                            <div className="tr--1 prl-a-i">
                                <svg viewBox="0 0 991 1300"><path className="st0" d="M722.9,1616V407.5c0-33,26.8-59.8,59.8-59.8h148.4c33,0,59.8,26.8,59.8,59.8V1616H722.9z" /><path className="st01" d="M962,266.9L962,266.9c0-18.5-15-33.5-33.5-33.5H815c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h113.5C947,300.5,962,285.5,962,266.9z" /><path className="st02" d="M956,158.9L956,158.9c0-18.5-15-33.5-33.5-33.5H890c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h32.5C941,192.5,956,177.5,956,158.9z" /><path className="st1" d="M494.1,1616V552.8c0-33,26.8-59.8,59.8-59.8h187.8c33,0,59.8,26.8,59.8,59.8V1616H494.1z" /><path className="st2" d="M0.6,1616V986.1c0-33,26.8-59.8,59.8-59.8h41.9c33,0,59.8,26.8,59.8,59.8V1616H0.6z" /><path className="st3" d="M296.3,1616V933.9c0-33-26.8-59.8-59.8-59.8H133.3c-33,0-59.8,26.8-59.8,59.8V1616H296.3z" /><path className="st31" d="M228.2,796.9L228.2,796.9c0-18.5-15-33.5-33.5-33.5H130c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h64.6C213.1,830.5,228.2,815.5,228.2,796.9z" /><path className="st4" d="M243.1,1616V628.8c0-33,26.8-59.8,59.8-59.8h52.4c33,0,59.8,26.8,59.8,59.8V1616H243.1z" /><path className="st5" d="M356,1616V807.1c0-33,26.8-59.8,59.8-59.8h110.1c33,0,59.8,26.8,59.8,59.8V1616H356z" /><path className="st51" d="M561,670.9L561,670.9c0-18.5-15-33.5-33.5-33.5H414c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h113.5C546,704.5,561,689.5,561,670.9z" /></svg>
                            </div>
                        </div>
                        <div className="scroll-down" ref={scrollDown}><svg viewBox="0 0 90 90"><path d="M34.7,67.5c-11.3,0-20.5-9.2-20.5-20.5V22c0-11.3,9.2-20.5,20.5-20.5S55.3,10.7,55.3,22v25C55.3,58.3,46.1,67.5,34.7,67.5z"></path><circle className="sr-d" cx="34.3" cy="21.6" r="5"></circle></svg></div>
                    </div>
                </section>
                <section className="panel red" >
                    <div className="container">
                        <p><span className="line line-1"></span><br />This line's animation will begin when it enters the viewport and finish when its top edge hits the top of the viewport, staying perfectly in sync with the scrollbar because it has <code>scrub:&nbsp;true</code></p>
                    </div>
                </section>
                <div style={{ position: 'relative', overflowX: "hidden" }}>
                    <section className="panel orange" id="quote">
                        <img className="bp-7" src={img7} alt="description of" />
                        <div style={{ position: 'relative', zIndex: 2 }}>
                            <div className="container">
                                <p><span className="line line-2"></span><br />This orange panel gets pinned when its top edge hits the top of the viewport, then the line's animation is linked with the scroll position until it has traveled 100% of the viewport's height (<code>end: "+=100%"</code>), then the orange panel is unpinned and normal scrolling resumes. Padding is added automatically to push the rest of the content down so that it catches up with the scroll when it unpins. You can set <code>pinSpacing: false</code> to prevent that if you prefer.</p>
                            </div>
                        </div>
                    </section>
                </div>

                <section id="sectionPin">
                    <div className="pin-wrap" style={{ background: '#4e9cb0' }}>
                        <BrowserView>
                            <div style={{ width: '60vw' }}><h2>My most recent works as a Motion Designer, Designer, Web designer and Web developer</h2></div>
                        </BrowserView>
                        <MobileView>
                            <h2>My most recent works as a Motion Designer, Designer, Web designer and Web developer</h2>
                        </MobileView>
                        {(error) ? <div className="l--data" ref={() => setcontLoaded(true)} style={styleDataJson.loadError}><h4>Error: {error.message}</h4></div> : (!isLoaded) ? <div style={styleDataJson.loadError}><h4>Loading...</h4></div> :
                            <>
                                {itemsData.slice(-4).reverse().map(item => {
                                    const { id, title, titleimage } = item;
                                    return (
                                        <div key={id} style={{ position: 'relative' }} ref={() => setcontLoaded(true)}>
                                            <div className="img-hor skewElem" data-crsr-eye>
                                                <Link to={`/blog/p-n/${id}`}>
                                                    <div className="img-cont" style={{ backgroundImage: `url(${titleimage})` }} />
                                                </Link>
                                            </div>
                                            <h3 className="skewElemPrlx">{title}</h3>
                                        </div>
                                    )
                                }
                                )}
                            </>
                        }
                        <BrowserView>
                            <Link to="/blog" data-crsr-more>
                                <div className="img-hor" style={{ width: '34vw', marginRight: '10vw' }}>
                                    <h2>and More</h2>
                                </div>
                            </Link>
                        </BrowserView>
                    </div>
                </section>
                <section className="panel gray" id="done">
                    WELL DONE!
                </section>
                <Footer />
            </div>
        </>
    )
}

export default Home;